// This is entrypoint, which `React.render` takes place after DOM is ready.
import React, { Suspense, lazy } from "react";
import classnames from "classnames";
import { Route, Router, Switch } from "react-router-dom";
import { render } from "react-dom";
import { Provider } from "react-redux";
import { selectOrCreate } from "fancyutils";
import { history } from "common-components";
import Bugsnag from "@bugsnag/js";
import BugsnagPluginReact from "@bugsnag/plugin-react";

if (__Config.bugsnag) {
    Bugsnag.start({
        apiKey: "a54d48e98cb0410b7bdf0e60a905b00e",
        plugins: [new BugsnagPluginReact()],
        user: {
            id: window.__FancyUser?.id || null
        },
    });
}

import { attachEntryEvents } from "./container/entry-events";
import { pagingContext } from "./container/routeutils";
import store from "./store/store";
import { historyHook } from "./container/history";
import { closeOverlay } from "./action/action-helpers";

const App: React.FC = ({ children }) => {
    if (__Config.design_v4) {
        return <>{children}</>;
    }

    return (
        <div className={classnames("popup overlay-thing thing-detail", { new: !!window.__Config.heart_icon })}>
            {children}
            <a
                className="ly-close"
                onClick={(event) => {
                    event.preventDefault();
                    event.stopPropagation();
                    store.dispatch(closeOverlay());
                }}>
                Close
            </a>
        </div>
    );
};

$(document).ready(() => {
    require("./Shared");

    const overlayContainer = __Config.design_v4
        ? document.querySelector("#container-wrapper .container")
        : selectOrCreate("#overlay-thing");

    const Overlay = __Config.design_v4
        ? lazy(() => import(/* webpackChunkName: "OverlayThing.main" */ "./components/v2"))
        : lazy(() => import(/* webpackChunkName: "OverlayThing.main.old" */ "./components/Overlay"));

    const ErrorBoundary = __Config.bugsnag ? Bugsnag.getPlugin("react")!.createErrorBoundary(React) : React.Fragment;

    const tree = (
        <ErrorBoundary>
            <Provider store={store}>
                <Router history={history}>
                    <App>
                        <Switch>
                            <Route path="/things/*">
                                <Suspense fallback={<div />}>
                                    <Overlay />
                                </Suspense>
                            </Route>
                            <Route path="/sales/*">
                                <Suspense fallback={<div />}>
                                    <Overlay />
                                </Suspense>
                            </Route>
                            <Route path="*" component={() => null} />
                        </Switch>
                    </App>
                </Router>
            </Provider>
        </ErrorBoundary>
    );
    render(tree, overlayContainer, () => {
        history.listen(historyHook);
        historyHook({ pathname: location.pathname, search: location.search }); // should init once
        attachEntryEvents();
        pagingContext.init();
    });
});
